.search-input {
  box-sizing: border-box;
  background-color: #fff;
  height: 48px;
  border: none !important;
  box-shadow: 0 2px 8px #00000008;
  width: 280px;
  margin: 10px;
}

.search-result-card {
  position: absolute;
  overflow: auto;
  width: 280px;
  margin: 10px;
  top: 60px;
  bottom: 0;
}

svg {
  vertical-align: baseline;
  width: 40px;
  height: 40px;
  margin-left: -5px;
}

.state-icons {
  width: 100px;
  position: absolute;
  top: 8px;
  right: 0;
}

.device-title {
  height: 40px;
  position: relative;
}

.state-icons svg {
  width: 25px;
  height: 25px;
}

.device-icon path {
  fill: #333;
}

.device-option {
  height: 40px;
  font-size: 18px;
  line-height: 40px;
  font-weight: 300;
  letter-spacing: 0.25px;
  color: rgb(33, 37, 41);
  display: inline-block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 110px;
}

.menu-container {
  overflow-x: hidden;
  overflow-y: auto;
  width: 300px;
  display: block;
  position: absolute;
  bottom: 132px;
  top: 220px;
  padding-top: 10px;
  background: #f9f9f9;
}

.device-list {
  width: 300px;
  position: absolute;
  top: 79px;
  bottom: 3px;
  overflow: auto;
}

.device-list > li {
  padding: 5px 15px;
}

.device-list > li:hover {
  background: #f1f1f1;
}

.selected {
  background: #eee;
}

.selected * {
  fill: #00aeef !important;
  color: #00aeef !important;
}

.iconButton {
  cursor: pointer;
}

.iconButton:hover * {
  fill: #00aeef;
}

.tracking-icon * {
  margin: 7px;
  float: right;
  fill: #00aeef;
}

.recording-icon * {
  margin: 0 2px;
  float: right;
  color: red !important;
}

.meta-title {
  width: 90px;
  display: inline-block;
}

::ng-deep .streaming {
  background: #eee !important;
  box-shadow: 0px 1px 3px #ccc inset !important;
}

::ng-deep .mat-mdc-list-item {
  border-bottom: 1px #eee solid;
  padding: 10px 20px !important;
}

::ng-deep .mdc-list-item__secondary-text::before {
  height: 0px !important;
  display: block !important;
}

::ng-deep .mdc-list-item__secondary-text {
  margin-top: 20px !important;
}

::ng-deep .meta-data .mdc-list-item__secondary-text {
  margin-top: 5px !important;
}

::ng-deep .mdc-list-item.mdc-list-item--with-three-lines {
  height: auto !important;
}

::ng-deep .mdc-list-item__primary-text {
  height: 40px;
}

::ng-deep .mdc-list-item.mdc-list-item--with-two-lines {
  height: auto !important;
}

::ng-deep search-menu .mdc-button {
  width: 96%;
  margin: 10px 5px;
}
